* {
    font-family: 'Poppins', sans-serif;
    -webkit-user-select: none;
    -moz-user-select: -moz-none;
    -o-user-select: none;
    user-select: none;
}

img {
    -webkit-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
}

img {
    pointer-events: none;
}

.movie_card {
    padding: 0 !important;
    width: 23%;
    margin: 30px 6px;
    border-radius: 10px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.2), 0 4px 15px 0 rgba(0, 0, 0, 0.19);
    float: left;
}

.movie_card img {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    height: 20rem;
}

.movie_info {
    color: #5e5c5c;
}

.movie_info i {
    font-size: 15px;
}

.card-title {
    width: 80%;
    height: 4rem;
}

.play_button {
    background-color: #ff3d49;
    position: absolute;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    right: 20px;
    bottom: 111px;
    font-size: 27px;
    padding-left: 21px;
    padding-top: 16px;
    color: #FFFFFF;
    cursor: pointer;
}

.credits {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 8px;
    border: 2px solid #8e24aa;
    font-size: 18px;
}

.credits .card-body {
    padding: 0;
}

.credits p {
    padding-top: 15px;
    padding-left: 18px;
}

.credits .card-body i {
    color: #8e24aa;
}
@media only screen and (max-width:992px){
    .movie_card{
        width: 30%;
    }
}
@media only screen and (max-width:768px){
    .movie_card{
        width: 47%;
    }
}
@media only screen and (max-width:600px){
    .container{
        width: 100%;
        margin: 10px auto;
        overflow: hidden;
    }
    .movie_card{
        width: 280px;
        margin: 10px auto;
        float: none;
    }
}